* {
  box-sizing: border-box;
  font-family: 'IBM Plex Sans', sans-serif !important;
}

html,
body {
  margin: 0 auto;
  padding: 0;
}

body {
  overscroll-behavior: none;
}


ul {
  margin-left: 2em;
}

#root {
  overflow-x: hidden;
}

.iconLink {
  color: gray;
}

.iconLink:hover {
  color: #57068c;
}